    padding-bottom: 4rem;
}

.our-story .general-section-subcontainer {
    max-width: 1000px;
    text-align: justify;
}

.our-story .our-story-subheading {
    font-size: 1.2rem;
}

.our-story .general-section-subcontainer p {
    margin-bottom: 1.5rem;
