___CSS_LOADER_EXPORT___.push([module.id, `/* #region Why Choose Us Styling */
.why-choose-us {
    background-color: #f6f3ef;
    padding: 4rem 0;
}

.why-choose-us-cards-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
    gap: 2rem;

    justify-content: center;
    justify-items: stretch;

    align-items: stretch;
}

.why-choose-us-cards-container>.card {

    /* Compatibility */
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 0.75rem;
    overflow: hidden;
    background-color: white;

    transition: transform 0.3s, box-shadow 0.3s;
    /* Compatibility */

    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 2.5rem 2.5rem;
    height: auto;
}

.why-choose-us-cards-container>.card:hover {
    transform: translateY(-0.5%);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);

}

.why-choose-us-card-icon>img {
    object-fit: contain;
    object-position: center;

    width: 2.5rem;
}

.why-choose-us-card-text {
    padding-top: 1rem;
}

.why-choose-us-card-text>h3 {
    font-size: 1.25rem;
    margin-bottom: 1rem;
    color: #34402e;
    text-align: center;
}

.why-choose-us-card-text>h4 {
    text-align: center;
    margin-bottom: 1rem;
    margin-top: -0.75rem;

    color: hsl(100, 0%, 37%);

}


.why-choose-us-card-text>p {
    color: #4b5563;
    text-align: center;
}

/* #endregion Why Choose Us Styling */
