___CSS_LOADER_EXPORT___.push([module.id, `:root {
    /* Header Vars */
    --transparent-bg-font-color: white;
    --light-mode-bg-font-color: black;
    --hover-highlight-color: #008cff33;
    --light-mode-bg-color-primary: rgba(255, 255, 255, 0.90);

    --scroll-bar-width: 7px;

    scroll-behavior: smooth;
}

body {
    min-height: 100vh;
    font-family: "Noto Sans", sans-serif;
    font-optical-sizing: auto;
    position: relative;
    z-index: 0;

    background-color: #fcfbf8;
}

/* #region Scroll bar styiling */
::-webkit-scrollbar {
    width: var(--scroll-bar-width);
}

::-webkit-scrollbar-track {
    background-color: white;
}

::-webkit-scrollbar-thumb {
    background-color: #869faf;
    border-radius: 10px;
    /* border: 1px solid transparent; */
    background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
    background-color: #929292;
}

.content {
    margin-top: 20rem;
}

/* #endregion Scroll bar styiling */

/* #region Skeleton Loading */

.skeleton {
    /* opacity: 0.7; */
    animation: skeleton-loading 1s linear infinite alternate;
    width: 100%;
}

@keyframes skeleton-loading {
    0% {
        background-color: hsl(200, 20%, 70%);
    }

    100% {
        background-color: hsl(200, 20%, 95%);
    }
}

.skeleton-text,
.skeleton-text:first-child {
    width: 100%;
    height: 0.8rem;
    border-radius: 0.5rem;
    margin-bottom: 0.5rem;
}

.skeleton-text:last-child {
    margin-bottom: 0;
    width: 75%;
}

/* #endregion Skeleton Loading */

/* #region general section styling */

.general-section {
    padding-top: 4rem;
    background-color: inherit;
}


.general-section-heading {
    text-align: center;
    width: 90%;
    max-width: 1400px;
    margin: auto;
    margin-bottom: 3rem;
}

.general-section-heading h2 {
    font-size: 2.25rem;
    font-family: "PT Serif", serif;

    line-height: 1.25;
}

.general-section-subcontainer {
    padding: 0 1rem;
    margin: auto;
    max-width: 1400px;
}

.general-section.misc-hero {
    padding: 0 0;
    min-height: 60lvh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #0f1019;
    color: rgba(255, 255, 255, 0.9);
}

.misc-hero .general-section-heading {
    margin: 0;
    margin-top: 2rem;
}

.misc-hero .general-section-heading h2 {
    font-size: clamp(2.75rem, 5vw, 3rem);
}

/* #endregion general section styling */
